body {
  display: flex;
  align-items: center;
  max-width: 800px;
  margin: 20px auto;
}

.toolbar {
  margin-bottom: 10px;
}

button {
  margin-right: 10px;
  padding: 5px 10px;
  background-color: white;
  border: 1px solid rgb(100, 100, 100) ;
}

button.active {
  color: white;
  background-color: gray;
}

.projectList {
  max-height: 1500px;
  display: flex;
  align-content: flex-start;
  flex-direction: column;
  flex-wrap: wrap;
}

.image {
  display: flex;
  width: 250px;
  height: auto;
  margin: 0 10px 10px 0;
  background: red;
}


img {
  width: 100%;
  height: 100%;
}